@mixin pagination-prev {
  border: none;
  background-color: transparent;
  position: relative;
  right: auto;
  left: auto;
  top: auto;
  width: 22px;
  height: 22px;
  margin: 0;

  &::after {
    content: "";
    background-image: url("../img/stack.svg#arrow-blue");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 10px;
    transform: rotate(225deg);
    width: 10px;
    height: 10px;
  }

}

@mixin pagination-next {
  border: none;
  background-color: transparent;
  position: relative;
  right: auto;
  left: auto;
  top: auto;
  width: 22px;
  height: 22px;
  margin: 0;

  &::after {
    content: "";
    background-image: url("../img/stack.svg#arrow-blue");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 10px;
    transform: rotate(45deg);
    width: 10px;
    height: 10px;
  }
}
